html, body {
    height: 100%;
    background-image: url("./images/background.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    font-family: Avenir, Georgia, 'Times New Roman', Times, serif;
}

body {
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    -ms-flex-pack: center;
    -webkit-box-pack: center;
    justify-content: center;
}

.navbar {
    margin: 0px -16px;
}

#headline {
    border-bottom: 2px solid #aaaaaa;
    color: #ffffff;
    text-align: center;
}

.card {
    text-align: left;
    margin-bottom: 10px;
}

.modal {
    text-align: left;
}

footer {
    color: #ffffff;
    text-align: center;
}

.fa-red {
    color: #d9534f;
}